import React from "react";
import ContentEditable from "react-contenteditable";
import {compose} from "redux";
import withSendMessageHandlers from "../../../HOC/withSendMessageHandlers";
import sendMessageImg from "../../../assets/img/sendMessage.png";
import {scrollTo} from "../../../common/JS/scrollTo";
import 'emoji-mart/css/emoji-mart.css'
import {Picker} from "emoji-mart";


// ÐÑÐ¸ÑÐ»Ð¾ÑÑ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÑ ÐºÐ»Ð°ÑÑÐ¾Ð²ÑÐ¹ ÐºÐ¾Ð¼Ð¿Ð½ÐµÐ½Ñ, ÑÐ°Ðº ÐºÐ°Ðº Ð¿ÑÐ¸ ÑÑÐ½ÐºÑÐ¸Ð¾Ð½Ð°Ð»ÑÐ½Ð¾Ð¼, ContentEditable Ð½Ðµ ÑÐ¾ÑÐµÐ»
// Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÑÑ ÑÐ²Ð¾Ð¸ Ð¿ÑÐ¾Ð¿ÑÑ Ð¸ Ð¿ÑÐ¸Ð½Ð¸Ð¼Ð°ÑÑ ÑÑÐ½ÐºÑÐ¸Ñ handleEnterButton ÑÐ¾ Ð°ÐºÑÑÐ°Ð»ÑÐ½ÑÐ¼Ð¸ Ð·Ð°Ð¼ÑÐºÐ°Ð½Ð¸ÑÐ¼Ð¸.
class ChatField extends React.PureComponent {

  state = {
    isEmoji: false
  };

  //2 ÑÑÐ½ÐºÑÐ¸Ð¸ Ð½Ð¸Ð¶Ðµ Ð¿ÑÐ¾ÐºÑÑÑÐ¸Ð²Ð°ÑÑ Ð²Ð²ÐµÑÑ ÑÐ¿Ð¸ÑÐ¾Ðº ÐºÐ¾Ð½ÑÐ°ÐºÑÐ¾Ð² Ð¿Ð¾ÑÐ»Ðµ Ð¾ÑÐ¿ÑÐ°Ð²ÐºÐ¸ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ñ,
  //ÑÑÐ¾Ð±Ñ ÑÐ·ÐµÑ Ñ ÐºÐ¾ÑÐ¾ÑÐ¾Ð¼ Ð¸Ð´ÐµÑ Ð´Ð¸Ð°Ð»Ð¾Ð³ Ð±ÑÐ» Ð² Ð¿Ð¾Ð»Ðµ Ð²Ð¸Ð´Ð¸Ð¼Ð¾ÑÑÐ¸.
  handleMouseClick = () => {
    this.props.sendMessage();
    scrollTo.top.element(this.props.contactsListElem);
  };

  handleEnterButton = (event) => {
    if (event.keyCode !== 13) return;
    if (event.shiftKey) return;
    this.props.handleEnterButton(event);
    scrollTo.top.element(this.props.contactsListElem);
  };

  toggleEmoji = () => {
    this.setState((state) => ({
        isEmoji: !state.isEmoji
      })
    )
  };

  render = () => {
    return (
      <div className="dialogs-field__chat-wrapper">
        {
          this.state.isEmoji &&
          <Picker style={{position: 'absolute', left: "32rem", top: "-20rem", zIndex: 1000}}
                  emojiSize={"1.25rem"} title='' perLine={8} skin={1} set={'emojione'}
                  sheetSize={32}
                  onSelect={(emoji) => {
                    this.props.insertEmoji(
                      emoji.native
                    )
                  }}
          />
        }
        <label className="dialogs-field__send-button-wrapper">
          <img className="send-button__img" alt="" src={sendMessageImg}/>
          <button className="send-button__button"
                  onClick={this.handleMouseClick}
          />
        </label>
        <div className="dialogs-field__emoji-button emoji-button" onClick={this.toggleEmoji}/>
        <div className="dialogs-field__chat">
          <ContentEditable html={this.props.state.messageText}
                           disabled={false}
                           tagName={"div"}
                           className={""}
                           placeholder={"Type your message . . ."}
                           onChange={this.props.handleOnChange}
                           onKeyDown={this.handleEnterButton}
          />
        </div>
      </div>
    )
  }
}

export default compose(
  withSendMessageHandlers
)(ChatField);
