import React, {useCallback, useState} from "react";
import ProfileInfoForm from "./ProfileInfoForm";
import ProfileDescription from "./ProfileDescription";
import ProfileAvatar from "./ProfileAvatar";

const ProfileInfo = React.memo((props) => {

  const [editMode, setEditMode] = useState(false);

  const handleSubmit = useCallback(
    (userData) => {
      props.updateUserData(userData);
      setEditMode(false);
    }, []);

  const activateEditMode = useCallback(
    () => {
      setEditMode(true);
    }, []);

  const deactivateEditMode = useCallback(
    () => {
      props.toggleProfileIsUpdating(false);
      setEditMode(false);
    }, []);

  //Ð¾ÑÐ¾Ð±ÑÐ°Ð¶Ð°ÑÑ ÑÐ¾Ð»ÑÐºÐ¾ Ð² ÑÐ»ÑÑÐ°Ðµ ÐºÐ¾Ð³Ð´Ð° Ð²ÑÐºÐ»ÑÑÐµÐ½ ÑÐ´Ð¸Ñ Ð¼Ð¾Ð´ Ð¸ Ð½Ðµ Ð¿ÑÐ¾Ð¸ÑÑÐ¾Ð´Ð¸Ñ Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¿ÑÐ¾ÑÐ¸Ð»Ñ (Ð² ProfileInfoForm )
  const descriptionOrForm = !editMode && !props.profileIsUpdating ?
    <ProfileDescription userData={props.userData}
                        isOwner={props.isOwner}
                        activateEditMode={activateEditMode}
                        userStatus={props.userStatus}
                        sendNewUserStatus={props.sendNewUserStatus}
                        ownerId={props.ownerId}
    />
    //initialValues Ð°Ð²ÑÐ¾Ð¼Ð°ÑÐ¸ÑÐµÑÐºÐ¸ ÑÐ°ÑÐºÐ¸Ð´ÑÐ²Ð°ÐµÑÑÑ Ð¿Ð¾ Ð¿Ð¾Ð»ÑÐ¼, ÑÐ°Ðº ÐºÐ°Ðº ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÑÑ Ð¸Ð¼ÐµÐ½Ð°
    : <ProfileInfoForm contacts={props.userData.contacts}
                       onSubmit={handleSubmit}
                       initialValues={props.userData}
                       deactivateEditMode={deactivateEditMode}
    />;


  return (
    <>
      <ProfileAvatar userAvatar={props.userData.photos}
                     isOwner={props.isOwner}
                     updateUserPhoto={props.updateUserPhoto}
                     ownerId={props.ownerId}
                     startChatting={props.startChatting}
                     history={props.history}
                     urlUserId={props.urlUserId}
      />
      {descriptionOrForm}
    </>
  );

});

export default ProfileInfo;