import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import {reducer as formReducer} from "redux-form";
import thunk from "redux-thunk"; //thunk middleware
import {profileReducer} from "../redux/reducers/profileReducer";
import {authReducer} from "./reducers/authReducer";
import {usersReducer} from "./reducers/usersReducer";
import {appReducer} from "./reducers/appReducer";
import {dialogsReducer} from "./reducers/dialogsReducer";
import {reduxFormReducer} from "./reducers/reduxFormReducer";


const reducers = combineReducers({
  auth: authReducer,
  dialogsPage: dialogsReducer,
  profilePage: profileReducer,
  usersPage: usersReducer,
  mainApp: appReducer,
  form: formReducer.plugin(reduxFormReducer)
});

//store Ñ Ð¿Ð¾Ð´Ð´ÐµÑÐ¶ÐºÐ¾Ð¹ ÑÐ°Ð±Ð¾ÑÑ ÑÐ°ÑÑÐ¸ÑÐµÐ½Ð¸Ñ Ð´Ð»Ñ ÑÑÐ¾Ð¼Ð°
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(
  reducers,
  composeEnhancers(applyMiddleware(thunk))
);

//ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ðµ Ð¾Ð±ÑÑÐ½Ð¾Ð³Ð¾ ÑÑÐ¾ÑÐ°
// const store = createStore(reducers, applyMiddleware(thunk));

window.store = store;

export default store;