import React, {useEffect} from 'react'
import './News.css'

const News = () => {

  useEffect(() => {
    document.title = "News";
  }, []);

  return (
    <div className='work-in-progress__wrapper'>
      <div className="work-in-progress">
        Work in progress
      </div>
    </div>
  )
};

export default News