import React from "react";

const withSendMessageHandlers = (Component) => {
  return class Wrapper extends React.PureComponent {

    state = {
      messageText: ""
    };

    sendMessage = async () => {
      let messageText = this.state.messageText.replace(/<[^>]+>/g, '');
      if (messageText) {
        await this.props.sendNewMessage(this.state.messageText);
        this.setState({
          messageText: ""
        });
      }
    };

    handleEnterButton = (event) => {
      if (event.keyCode !== 13) return;
      if (event.shiftKey) return;
      this.sendMessage(this.state.messageText);
      event.preventDefault();
    };

    handleOnChange = (e) => {
      this.setState({
        messageText: e.target.value
      });
    };

    insertEmoji = (smiley) => {
      this.setState((state)=>({
        messageText: `${state.messageText}${smiley}`
      }));
    };

    render () {
      return (
        <Component state={this.state}
                   sendMessage={this.sendMessage}
                   handleEnterButton={this.handleEnterButton}
                   handleOnChange={this.handleOnChange}
                   insertEmoji ={this.insertEmoji}
                   {...this.props}
        />
      )
    }
  }
};

export default withSendMessageHandlers;