export const scrollTo = {
  bottom: {
    element: (elem) => {
      const scrollHeight = elem.scrollHeight; //Ð¿Ð¾Ð»Ð½Ð°Ñ Ð²ÑÑÐ¾ÑÐ° Ñ ÑÑÐµÑÐ¾Ð¼ Ð¿ÑÐ¾ÐºÑÑÑÐºÐ¸ Ð¸ Ð²Ð¸Ð´Ð¸Ð¼Ð¾Ð¹ ÑÐ°ÑÑÐ¸
      const height = elem.clientHeight; //Ð²ÑÑÐ¾ÑÐ° Ð²Ð¸Ð´Ð¸Ð¼Ð¾Ð¹ ÑÐ°ÑÑÐ¸

      elem.scrollTop = scrollHeight - height; //ÑÑÑÐ°Ð½Ð¾Ð²ÐºÐ° ÑÐµÐºÑÑÐµÐ¹ Ð¿ÑÐ¾ÐºÑÑÑÐºÐ¸
    },
    document: () => {
      const scrollHeight = Math.max(
        document.body.scrollHeight, document.documentElement.scrollHeight,
        document.body.offsetHeight, document.documentElement.offsetHeight,
        document.body.clientHeight, document.documentElement.clientHeight
      );
      const height = document.documentElement.clientHeight;

      window.scrollTo(0, scrollHeight - height);
    }
  },
  top: {
    element: (elem) => {
      elem.scrollTop = 0;
    },
    document: () => {
      window.scrollTo(0, 0);
    }
  }
};




