import React, {useCallback} from 'react';
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../utils/validators";
import {Input} from "../../common/FormControl/FormControl"


const LoginForm = React.memo((props) => {

  const maxLength = useCallback(maxLengthCreator(50), []);

  return (
    //handleSubmit - Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸Ðº Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½ÑÐ¹ reduxForm
    //Ð²Ð½ÑÑÑÐ¸ ÑÐµÐ±Ñ Ð¾Ð½ Ð²ÑÐ·Ð¾Ð²ÐµÑ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½ÑÐ¹ Ð½Ð°Ð¼Ð¸ Ð² props onSubmit
    <form onSubmit={props.handleSubmit} className="login-form">
      <Field className="login-form__email-input"
             type={"text"}
             name={"email"}
             component={Input}
             placeholder={"email"}
             validate={[required, maxLength]}
             wrapperClassName={"login-form__input-body"}
      />
      <Field className="login-form__password-input"
             type={"password"}
             name={"password"}
             component={Input}
             placeholder={"password"}
             validate={[required, maxLength]}
             wrapperClassName={"login-form__input-body"}
      />
      <Field className="login-form__checkbox"
             wrapperClassName={"login-form__checkbox-body"}
             type="checkbox" name="rememberMe"
             component={Input}
             textleft="remember me"

      />
      {props.captchaUrl &&
      <div className="login-form__captcha">
        <img alt="captcha"
             className="login-form__captcha-img"
             src={props.captchaUrl}
        />
      </div>}
      {props.captchaUrl &&
      <Field className="login-form__captcha-input"
             type={"text"}
             name={"captcha"}
             component={Input}
             validate={[required, maxLength]}
             wrapperClassName={"login-form__input-body"}
      />}
      {/*ÐÑÐ¸Ð½ÑÑÐ¾Ð½Ð½Ð°Ñ Ð¾ÑÐ¸Ð±ÐºÐ°*/}
      {props.error ? <div className="login-form__async-error">{props.error}</div> : null}
      <label className="login-form__button">
        <button className="login-form__hidden-button"/>
        Enter
      </label>
    </form>
  )
});

export default reduxForm({
  form: "login"
})(LoginForm);

