//Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ Ð³Ð¾ÑÐ¾Ð²ÑÐ¹ Ð´Ð»Ñ innerHtml Ð²ÑÑÐ°Ð²ÐºÐ¸ ÑÐµÐºÑÑ.
const parsers = [
  youtube
];


//ÐÐ»Ð°Ð²Ð½Ð°Ñ ÑÑÐ½ÐºÑÐ¸Ñ
const linkParser = (string, settings) => {
  let text = string;
  const div = document.createElement('div');
  div.innerHTML = text;
  const anchorsLength = div.getElementsByTagName('a').length;

  for (let i = 0; i < anchorsLength; i++) {
    const aElem = div.getElementsByTagName('a')[i];
    const outer = aElem.outerHTML;
    const link = aElem.innerHTML;

    for (let j = 0; j < parsers.length; j++) {
      //Ð¿ÑÐ¾ÑÐ¾Ð´Ð¸Ñ Ð¿Ð¾ Ð²ÑÐµÐ¼ Ð¿Ð°ÑÑÐµÑÐ°Ð¼ Ð¸ Ð¸ÑÐµÑ ÑÐ¾Ð²Ð¿Ð°Ð´ÐµÐ½Ð¸Ðµ Ð¿Ð¾ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÐºÐµ
      const result = (parsers[i](link, settings));

      if (result) {
        text = text.replace(`${outer}`, `${result}`);
        break
      }
    }
  }
  return text
};

export default linkParser;






//ÑÑÐ½ÐºÑÐ¸Ñ-Ð¿Ð°ÑÑÐµÑ
function youtube (link, settings) {
  const reg = /^(?:(?:https?:)?\/\/)?(?:www\.)?(?:m\.)?(?:youtu(?:be)?\.com\/(?:v\/|embed\/|watch(?:\/|\?v=))|youtu\.be\/)((?:\w|-){11})(?:\S+)?$/;

  if (!reg.test(link)) return;

  const parser = (link) => {
    const match = link.match(reg);
    const src = `https://www.youtube.com/embed/${match[1]}`;

    return `<br><iframe src=${src} frameBorder="0" class="youtube-embed"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen></iframe>`

  };

  return parser(link);
}



