import React from "react";
import ContentEditable from "react-contenteditable";
import sendMessageImg from "../../../assets/img/sendMessage.png";
import {compose} from "redux";
import withSendMessageHandlers from "../../../HOC/withSendMessageHandlers";
import 'emoji-mart/css/emoji-mart.css'
import {Picker} from 'emoji-mart'

class ChatField extends React.PureComponent {

  state = {
    isEmoji: false
  };

  toggleEmoji = () => {
    this.setState((state) => ({
        isEmoji: !state.isEmoji
      })
    )
  };

  //Ð¼ÐµÑÐ¾Ð´Ñ Ð¾ÑÐ¿ÑÐ°Ð²ÐºÐ¸ ÑÐ¾Ð¾Ð±ÑÐµÐ½Ð¸Ñ Ð±ÐµÑÑÑÑÑ Ð¸Ð· ÑÐ¾ÐºÐ°
  render = () => {
    return (
      <div className="profile-wall__chat-wrapper">
        {
          this.state.isEmoji &&
          <Picker style={{position: 'absolute', right: "0.5rem", bottom: "-19.7rem", zIndex: 2000, width: '19.125rem'}}
                  emojiSize={20} title='' skin={1} set={'emojione'}
                  sheetSize={32}
                  onSelect={(emoji) => {
                    this.props.insertEmoji(
                      emoji.native
                    )
                  }}
          />
        }

        <div className="profile-wall__chat">
          <label className="profile-wall__send-button-wrapper">
            <img className="send-button__img" src={sendMessageImg} alt=""/>
            <button className="send-button__button"
                    onClick={this.props.sendMessage}
            />
          </label>

          <div className="profile-wall__emoji-button emoji-button" onClick={this.toggleEmoji}/>

          <ContentEditable html={this.props.state.messageText}
                           disabled={false}
                           tagName={"div"}
                           className={"profile-wall__chat-field"}
                           placeholder={"Type your message . . ."}
                           onChange={this.props.handleOnChange}
                           onKeyDown={this.props.handleEnterButton}
          />
        </div>
      </div>
    )
  }
}

export default compose(
  withSendMessageHandlers
)(ChatField);