import React, {useEffect} from 'react';
import {connect} from "react-redux";
import {
  deleteMessage,
  getUserData,
  getUserStatus,
  likesControl,
  sendNewMessage,
  sendNewUserStatus,
  stopAnimation,
  toggleProfileIsUpdating,
  updateUserData,
  updateUserPhoto
} from "../../redux/reducers/profileReducer";
import {compose} from "redux/es/redux";
import Profile from "./Profile";
import {withRouter} from "react-router-dom";
import {startChatting} from "../../redux/reducers/dialogsReducer";
import {scrollTo} from '../../common/JS/scrollTo';


const ProfileContainer = React.memo((props) => {

  const urlUserId = props.match.params.userId;

  useEffect(() => {
    props.getUserStatus(urlUserId);
    props.getUserData(urlUserId);
  }, [urlUserId]);

  useEffect(() => {
    scrollTo.top.document();
  }, []);

  useEffect(() => {
    document.title = "Profile";
  }, []);

  //ÐÑÐ»Ð¸ id Ð² ÑÑÐµÐ¹Ñ Ð¾ÑÐ»Ð¸ÑÐ°ÐµÑÑÑ Ð¾Ñ id Ð² url (Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ Ð¿ÐµÑÐµÑÐµÐ» Ñ Ð¾Ð´Ð½Ð¾Ð¹ ÑÑÑÐ°Ð½Ð¸ÑÑ Ð¿ÑÐ¾ÑÐ¸Ð»Ñ Ð½Ð° Ð´ÑÑÐ³ÑÑ),
  // ÑÐ¾ ÑÐ½Ð°ÑÐ°Ð»Ð° Ð¾ÑÐ¿ÑÐ°Ð²Ð¸ÑÑ Ð¿ÑÑÑÑÑÐºÑ ÑÑÐ¾Ð±Ñ Ð½Ðµ Ð±ÑÐ»Ð¾ Ð¼ÐµÑÑÐ°Ð½Ð¸Ñ ÑÑÐ¶Ð¾Ð³Ð¾ Ð°Ð²Ð°ÑÐ°ÑÐ° Ð¸ Ð´ÑÑÐ³Ð¾Ð¹ Ð¸Ð½ÑÐ¾ÑÐ¼Ð°ÑÐ¸Ð¸
  const idIsEqual = +urlUserId === props.stateUserId;
  //ÑÐ²Ð»ÑÐµÑÑÑ Ð»Ð¸ ÑÐµÐ»Ð¾Ð²ÐµÐº ÑÐ¾Ð·ÑÐ¸Ð½Ð¾Ð¼ ÑÑÑÐ°Ð½Ð¸ÑÑ
  const isOwner = +urlUserId === props.ownerId;

  if (!idIsEqual) return null;

  return (
    <Profile userStatus={props.userStatus}
             sendNewUserStatus={props.sendNewUserStatus}
             userData={props.userData}
             updateUserData={props.updateUserData}
             profileIsUpdating={props.profileIsUpdating}
             isOwner={isOwner}
             userId={urlUserId}
             updateUserPhoto={props.updateUserPhoto}
             urlUserId={urlUserId}
             startChatting={props.startChatting}
             history={props.history}
             ownerId={props.ownerId}
             wallMessages={props.wallMessages}
             sendNewMessage={props.sendNewMessage}
             likesControl={props.likesControl}
             toggleProfileIsUpdating={props.toggleProfileIsUpdating}
             stopAnimation={props.stopAnimation}
             deleteMessage={props.deleteMessage}
    />
  )
});


const mapStateToProps = (state) => {
  return {
    userStatus: state.profilePage.status,
    ownerId: state.auth.id,
    userData: state.profilePage.userData,
    profileIsUpdating: state.profilePage.profileIsUpdating,
    stateUserId: state.profilePage.userData.userId,
    wallMessages: state.profilePage.wallMessages
  }
};

export default compose(
  withRouter,
  connect(mapStateToProps, {
    getUserStatus,
    sendNewUserStatus,
    getUserData,
    updateUserData,
    updateUserPhoto,
    startChatting,
    sendNewMessage,
    likesControl,
    toggleProfileIsUpdating,
    stopAnimation,
    deleteMessage
  })
)(ProfileContainer);
